body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Tahoma", sans-serif;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.center-wrapper {
  width: 280px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .center-wrapper {
    width: 708px;
  }
}
@media (min-width: 1220px) {
  .center-wrapper {
    width: 1220px;
  }
}
.btn {
  display: block;
  width: 280px;
  min-height: 28px;
  background-color: #68b738;
  color: white;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 12px;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: normal;
}
.btn:focus,
.btn:hover {
  background-color: #5eaa2f;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .btn {
    width: 245px;
    min-height: 35px;
    padding-top: 17px;
    font-size: 20px;
    line-height: 20px;
  }
}
.page-header {
  min-height: 65px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}
.page-header--catalog-and-filter {
  border-bottom: 2px solid #F2F2F2;
}
.page-header__logo {
  margin-top: 11px;
  width: 191px;
  height: 39px;
}
.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
.page-header__burger {
  width: 25px;
  height: 19px;
  background: none;
  padding: 0;
  margin: 0;
  margin-top: 24px;
  margin-bottom: 23px;
  border: none;
  background-image: url("../img/mobile-menu.svg");
  cursor: pointer;
}
.page-header__burger--opened {
  width: 19px;
  height: 19px;
  background: none;
  padding: 0;
  margin: 0;
  margin-top: 22px;
  margin-bottom: 23px;
  border: none;
  background-image: url("../img/m-close.svg");
}
@media (min-width: 768px) {
  .page-header {
    width: 100%;
    flex-wrap: nowrap;
    margin-left: auto;
    margin-right: auto;
    min-height: 98px;
  }
  .page-header--catalog-and-filter {
    border-bottom: none;
  }
  .page-header__logo {
    margin-top: 24px;
  }
  .page-header__logo img {
    width: 174px;
    height: 59px;
  }
  .page-header__burger {
    display: none;
  }
}
@media (min-width: 1220px) {
  .page-header {
    z-index: 1;
    min-height: 142px;
  }
  .page-header--index {
    background-image: linear-gradient(to left, #63a73a 50%, white 50%);
  }
  .page-header--index::after {
    content: "";
    position: absolute;
    width: 720px;
    height: 696px;
    right: 0;
    background-image: url("../img/index-background-desktop@1x.jpg");
    z-index: 1;
  }
  .page-header__logo {
    margin-top: 53px;
  }
  .page-header__logo img {
    width: 203px;
    height: 60px;
  }
}
@media (min-width: 1220px) and (min-resolution: 144dpi), (min-width: 1220px) and (min-resolution: 1.5dppx) {
  .page-header--index::after {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}
.main-nav {
  width: 100%;
  z-index: 2;
}
@media (max-width: 768px) {
  .main-nav--closed {
    display: none;
  }
  .main-nav--opened {
    display: block;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  display: block;
  padding-top: 24px;
  padding-bottom: 24px;
  width: auto;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  border-top: 1px solid #F2F2F2;
}
.main-nav__item:hover,
.main-nav__item:focus {
  opacity: 0.6;
}
.main-nav__item:active {
  opacity: 0.3;
}
.main-nav__item a {
  color: #111111;
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
    display: block;
    margin-top: 16px;
  }
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 27px;
  }
  .main-nav__item {
    flex-grow: 100;
    display: block;
    padding-top: 0;
    padding-bottom: 0;
    border: none;
    font-size: 18px;
    line-height: 25px;
    font-weight: normal;
    margin-right: 37px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
  .main-nav__item a {
    display: block;
    width: 100%;
  }
}
@media (min-width: 1220px) {
  .main-nav {
    padding-top: 30px;
  }
  .main-nav__item {
    font-size: 20px;
    margin-right: 35px;
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: white;
    top: 33px;
  }
  .main-nav__item--index a {
    color: white;
  }
  .main-nav__item--catalog-and-filter::after {
    background-color: #68b738;
  }
}
.page-name {
  position: relative;
}
.page-name--index {
  background-image: linear-gradient(#63a73a 305px, white 305px);
}
.page-name--index .center-wrapper {
  background-image: url("../img/index-background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  padding-top: 2px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-name--index .center-wrapper {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
.page-name__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
  color: white;
  text-align: center;
}
.page-name__title--catalog {
  display: block;
  color: #111111;
  text-align: left;
  margin: 0;
  padding: 0;
  margin-top: 25px;
  margin-bottom: 39px;
}
.page-name__description {
  font-size: 14px;
  line-height: 14px;
