  margin-top: 22px;
  display: flex;
  flex-direction: column;
}
.page-category__item {
  background-color: #F2F2F2;
  padding-top: 6px;
  padding-left: 21px;
  padding-right: 20px;
  margin-bottom: 18px;
  position: relative;
}
.page-category__item:last-child {
  margin-bottom: 0;
}
.page-category__title {
  color: #111111;
  font-size: 24px;
