.work {
  margin-bottom: 42px;
}
.work__title {
  color: #111111;
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
  margin-top: 15px;
}
.work__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}
.work__item {
  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  padding-left: 80px;
  margin-bottom: 26px;
  position: relative;
}
.work__item::before {
  content: "";
  position: absolute;
  display: block;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  top: -2px;
  left: 0;
}
.work__item::after {
  content: "";
  position: absolute;
  display: block;
